import S from 'fluent-json-schema'
import {
  ClientEntity,
  Locales,
  sClientEntity,
  sDateString,
  sLocales,
  sStringId,
  UnitTime,
  sUnitTime,
  PageSearchQuery,
  Page,
  sPageSearchQuery,
  sBCAddress,
  iBigInt,
  sBigInt,
} from '../core'
import {
  CAMPAIGN_FIELDS,
  CAMPAIGN_SORT_FIELDS,
  CAMPAIGNS_ROUTES_KEY,
} from './campaign.const'

/**
 * Client Vault interface
 */
export interface Campaign extends CampaignData, ClientEntity {}

export function sCampaign(isPartial?: boolean) {
  return S.object()
    .id('#campaign')
    .additionalProperties(false)
    .extend(sClientEntity(isPartial))
    .extend(sCampaignData(isPartial))
}

export interface CampaignData {
  code: string
  name: Locales
  description?: Locales
  rules?: CampaignRule[]
  referrals?: CampaignReferral[]
  boosts?: CampaignBoost[]
  startDate?: string
  endDate?: string
  link?: string
  galxeId?: number
  isSyncable?: boolean
}

export function sCampaignData(isPartial?: boolean) {
  return S.object()
    .additionalProperties(false)
    .prop('code', S.string())
    .prop('name', sLocales())
    .prop('description', sLocales())
    .prop('rules', S.array().items(sCampaignRule()))
    .prop('referrals', S.array().items(sCampaignReferral()))
    .prop('boosts', S.array().items(sCampaignBoost()))
    .prop('startDate', sDateString())
    .prop('endDate', sDateString())
    .prop('link', S.string())
    .prop('galxeId', S.number())
    .prop('isSyncable', S.boolean())
    .required(isPartial ? [] : ['code', 'name'])
}

export interface CampaignRule {
  code: string
  // Rule info
  name: Locales
  description?: Locales

  // Trigger rule: deposit or other kind of transaction
  trigger: CampaignRuleTrigger

  // Deposit type: for amount or timing
  deposit: CampaignRuleDeposit

  // Reward type: fixed amount or multiplier
  reward: CampaignReward

  // Distribution frequency: every day or every x time
  frequency: CampaignRuleFrequency

  // Vault IDs restriction
  vaultIds?: string[]
}

export function sCampaignRule() {
  return S.object()
    .additionalProperties(false)
    .prop('code', S.string())
    .required()
    .prop('name', sLocales())
    .required()
    .prop('description', sLocales())
    .prop('trigger', sCampaignRuleTrigger())
    .required()
    .prop('deposit', sCampaignRuleDeposit())
    .required()
    .prop('reward', sCampaignReward())
    .required()
    .prop('frequency', sCampaignRuleFrequency())
    .required()
    .prop('vaultIds', S.array().items(sStringId()))
}

export type CampaignRuleTrigger = 'DEPOSIT' | 'DEPOSIT_REQUEST'

export function sCampaignRuleTrigger() {
  return S.string().enum(['DEPOSIT', 'DEPOSIT_REQUEST'])
}

export interface CampaignRuleDeposit {
  type: CampaignRuleDepositType
  value: number
  tokenId?: string
}

export function sCampaignRuleDeposit() {
  return S.object()
    .additionalProperties(false)
    .prop('type', sCampaignRuleDepositType())
    .required()
    .prop('value', S.number())
    .required()
    .prop('tokenId', sStringId())
}

export type CampaignRuleDepositType =
  | 'BALANCE'
  | 'BALANCE_USP'
  | 'BALANCE_SUSP'
  | 'POOL_BALANCE'
  | 'AGE'

export function sCampaignRuleDepositType() {
  return S.string().enum([
    'BALANCE',
    'BALANCE_USP',
    'BALANCE_SUSP',
    'POOL_BALANCE',
    'AGE',
  ])
}

export interface CampaignReward {
  type: CampaignRewardType
  value: number
}

export function sCampaignReward() {
  return S.object()
    .additionalProperties(false)
    .prop('type', sCampaignRewardType())
    .required()
    .prop('value', S.number())
    .required()
}

export type CampaignRewardType = 'AMOUNT' | 'MULTIPLIER' | 'PERCENTAGE'

export function sCampaignRewardType() {
  return S.string().enum(['AMOUNT', 'MULTIPLIER', 'PERCENTAGE'])
}

export interface CampaignRuleFrequency {
  value: number
  unit: UnitTime
}

export function sCampaignRuleFrequency() {
  return S.object()
    .additionalProperties(false)
    .prop('value', S.number())
    .required()
    .prop('unit', sUnitTime())
    .required()
}

export interface CampaignBoost {
  code: string
  name: Locales
  description?: Locales
  operatorId?: string
  link?: string
  linkLabel?: Locales

  // Type and reward
  type: CampaignBoostType
  reward: CampaignReward

  // Time restriction
  startDate?: string
  endDate?: string

  // Vault IDs restriction
  vaultIds?: string[]
  tokenId?: string
  rules?: string[]
  pools?: string[]
}

export function sCampaignBoost() {
  return S.object()
    .additionalProperties(false)
    .prop('code', S.string())
    .required()
    .prop('name', sLocales())
    .required()
    .prop('type', sCampaignBoostType())
    .required()
    .prop('reward', sCampaignReward())
    .required()
    .prop('startDate', sDateString())
    .prop('endDate', sDateString())
    .prop('description', sLocales())
    .prop('operatorId', sStringId())
    .prop('link', S.string())
    .prop('linkLabel', sLocales())
    .prop('tokenId', sStringId())
    .prop('vaultIds', S.array().items(sStringId()))
    .description('VaultIds realted to the boost')
    .prop('rules', S.array().items(S.string()))
    .description('Rules realted to the boost')
    .prop('pools', S.array().items(S.string()))
    .description('Pools realted to the boost')
}

export type CampaignBoostType =
  | 'REFERRAL'
  | 'REFERRAL_FEE'
  | 'REFERRED'
  | 'STAKE'
  | 'DEPOSIT'
  | 'DEPOSIT_BALANCER'
  | 'DEPOSIT_NAPIER'
  | 'DEPOSIT_PENDLE'
  | 'DEPOSIT_EULER'
  | 'SUPPLY_EULER'
  | 'SUPPLY_TERM'

export function sCampaignBoostType() {
  return S.string().enum([
    'REFERRAL',
    'REFERRAL_FEE',
    'REFERRED',
    'STAKE',
    'DEPOSIT',
    'DEPOSIT_BALANCER',
    'DEPOSIT_NAPIER',
    'DEPOSIT_PENDLE',
    'DEPOSIT_EULER',
    'SUPPLY_EULER',
    'SUPPLY_TERM',
  ])
}

export interface CampaignReferral {
  code: string
  isActive: boolean
}

export function sCampaignReferral() {
  return S.object()
    .additionalProperties(false)
    .prop('code', sCampaignReferralCode())
    .required()
    .prop('isActive', S.boolean())
    .required()
}

export function sCampaignReferralCode() {
  return S.string().minLength(6).maxLength(8).pattern('^[a-zA-Z0-9]*$')
}

export interface CampaignsSearchQuery extends PageSearchQuery {
  code?: string | string[]
  startDate?: string
  'startDate:lt'?: string
  'startDate:lte'?: string
  'startDate:gt'?: string
  'startDate:gte'?: string
  endDate?: string
  'endDate:lt'?: string
  'endDate:lte'?: string
  'endDate:gt'?: string
  'endDate:gte'?: string
}

export function sCampaignsSearchQuery() {
  return S.object()
    .additionalProperties(false)
    .prop('code', S.array().maxItems(200).items(S.string()))
    .prop('tokenId', S.array().maxItems(200).items(sStringId()))
    .description('The token IDentifier')
    .prop('startDate', sDateString())
    .description('The epoch start date equal than')
    .prop('startDate:lt', sDateString())
    .description('The epoch start date less than')
    .prop('startDate:lte', sDateString())
    .description('The epoch start date less or equal than')
    .prop('startDate:gt', sDateString())
    .description('The epoch start date greater than')
    .prop('startDate:gte', sDateString())
    .description('The epoch start date greater or equal than')
    .prop('endDate', sDateString())
    .description('The epoch end date equal than')
    .prop('endDate:lt', sDateString())
    .description('The epoch start date equal than')
    .prop('endDate:lte', sDateString())
    .description('The epoch start date equal than')
    .prop('endDate:gt', sDateString())
    .description('The epoch start date equal than')
    .prop('endDate:gte', sDateString())
    .description('The epoch start date equal than')
    .extend(sPageSearchQuery(CAMPAIGN_FIELDS, CAMPAIGN_SORT_FIELDS))
}

export enum CampaignsRoutingKey {
  idleSync = 'pareto.campaign.sync',
}

export interface CampaignsClientModel {
  create: (body: CampaignData) => Promise<Campaign>
  search: (params?: CampaignsSearchQuery) => Promise<Page<Campaign>>
  searchAll: (params?: CampaignsSearchQuery) => Promise<Page<Campaign>>
  list: (params?: CampaignsSearchQuery) => Promise<Campaign[]>
  listAll: (params?: CampaignsSearchQuery) => Promise<Campaign[]>
  findOne: (params?: CampaignsSearchQuery) => Promise<Campaign | undefined>
  readOne: (params: CampaignsSearchQuery) => Promise<Campaign>
}

export enum CampaignRoutes {
  v1Create = `v1/${CAMPAIGNS_ROUTES_KEY}`,
  v1Delete = `v1/${CAMPAIGNS_ROUTES_KEY}/:campaignId`,
  v1Read = `v1/${CAMPAIGNS_ROUTES_KEY}/:campaignId`,
  v1Update = `v1/${CAMPAIGNS_ROUTES_KEY}/:campaignId`,
  v1Search = `v1/${CAMPAIGNS_ROUTES_KEY}`,
  v1Points = `v1/${CAMPAIGNS_ROUTES_KEY}/:campaignId/points`,
  v1Sync = `v1/${CAMPAIGNS_ROUTES_KEY}/:campaignId/sync`,
  v1Ranking = `v1/${CAMPAIGNS_ROUTES_KEY}/:campaignId/ranking`,
}

export enum CampaignErrorCodes {
  notFound = 'CAMPAIGN_NOT_FOUND',
  notDeletable = 'CAMPAIGN_NOT_DELETABLE',
  walletRequired = 'WALLET_REQUIRED',
  referralNotValid = 'REFERRAL_CODE_INVALID',
  referralCodeNotActive = 'REFERRAL_CODE_NOT_ACTIVE',
  rankOverflow = 'RANK_OVERFLOW',
}

export interface CampaignPointsQuery {
  walletId?: string
  walletAddress?: string
}

export function sCampaignPointsQuery() {
  return S.object()
    .additionalProperties(false)
    .prop('walletId', sStringId())
    .description('Wallet Identifier')
    .prop('walletAddress', sBCAddress())
    .description('Wallet address')
}

export interface CampaignPoints extends CampaignMetrics {
  vaults?: CampaignPointsVault[]
  boosts?: CampaignPointsBoost[]
  metrics?: { [key: string]: string | number }
}

export function sCampaignPoints() {
  return S.object()
    .additionalProperties(false)
    .prop('vaults', S.array().items(sCampaignPointsVault()))
    .prop('boosts', S.array().items(sCampaignPointsBoost()))
    .prop('metrics', sCampaignPointsMetrics())
    .extend(sCampaignMetrics())
}

export interface CampaignPointsVault extends CampaignMetrics {
  _id: string
}

export function sCampaignPointsVault() {
  return S.object()
    .additionalProperties(false)
    .prop('_id', sStringId())
    .required()
    .extend(sCampaignMetrics())
}

export function sCampaignPointsMetrics() {
  return S.object().additionalProperties(true)
}

export interface CampaignMetrics {
  points: string
  perDay: string
  multiplierScaled?: string
  multiplier: string
  rewards?: CampaignMetricsReward[]
  code?: string
  rules?: string[]
}

export interface CampaignMetricsReward {
  tokenId?: string
  amount?: iBigInt
  USD: iBigInt
}

export function sCampaignMetricsReward() {
  return S.object()
    .additionalProperties(false)
    .prop('USD', sBigInt())
    .required()
    .prop('tokenId', sStringId())
    .prop('amount', sBigInt())
}

export function sCampaignMetrics() {
  return S.object()
    .prop('points', S.string())
    .required()
    .prop('perDay', S.string())
    .required()
    .prop('multiplier', S.string())
    .required()
    .prop('multiplierScaled', S.string())
    .prop('rewards', S.array().items(sCampaignMetricsReward()))
    .prop('code', S.string())
    .prop('rules', S.array().items(S.string()))
}

export interface CampaignPointsBoost extends CampaignMetrics {
  code: string
}

export function sCampaignPointsBoost() {
  return S.object()
    .additionalProperties(false)
    .prop('code', S.string())
    .required()
    .extend(sCampaignMetrics())
}

export interface CampaignRankingQuery {
  walletId?: string | string[]
  walletAddress?: string | string[]
  rank?: number | number[]
}

export function sCampaignRankingQuery() {
  return S.object()
    .additionalProperties(false)
    .prop('walletId', S.array().minItems(1).maxItems(200).items(sStringId()))
    .prop(
      'walletAddress',
      S.array().minItems(1).maxItems(200).items(sBCAddress())
    )
    .prop('rank', S.array().minItems(1).maxItems(200).items(S.number()))
}

export interface CampaignRanking {
  totalPoints: number
  totalWallets: number
  totalWalletsWithPoints: number
  rankings: CampaignRankingWallet[]
  updatedAt?: string
}

export function sCampaignRanking() {
  return S.object()
    .additionalProperties(false)
    .prop('totalPoints', S.number())
    .required()
    .prop('totalWallets', S.number())
    .required()
    .prop('totalWalletsWithPoints', S.number())
    .required()
    .prop('rankings', S.array().items(sCampaignRankingWallet()))
    .required()
    .prop('updatedAt', S.string())
}

export interface CampaignRankingWallet {
  walletId: string
  walletAddress: string
  points: number
  perDay: number
  rank: number
}

export function sCampaignRankingWallet() {
  return S.object()
    .additionalProperties(false)
    .prop('walletId', sStringId())
    .required()
    .prop('walletAddress', sBCAddress())
    .required()
    .prop('points', S.number())
    .required()
    .prop('perDay', S.number())
    .required()
    .prop('rank', S.number())
    .required()
}
